@import url(https://fonts.googleapis.com/css2?family=Righteous&family=Raleway:wght@500&family=Comfortaa&family=Allison&family=Oswald&family=Roboto&display=swap);

.fade-in-up {
    opacity: 0;
    -webkit-transform: translateY(20vh);
            transform: translateY(20vh);
    visibility: hidden;
    transition: opacity 0.3s ease-out, -webkit-transform 0.6s ease-out;
    transition: opacity 0.3s ease-out, transform 0.6s ease-out;
    transition: opacity 0.3s ease-out, transform 0.6s ease-out, -webkit-transform 0.6s ease-out;
    will-change: opacity, visibility;
}

.fade-in-up.is-visible {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
    visibility: visible;
}

.fade-in-left {
    opacity: 0;
    -webkit-transform: translateX(10vw);
            transform: translateX(10vw);
    visibility: hidden;
    transition: opacity 0.5s ease-out, -webkit-transform 0.5s ease-out;
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
    transition: opacity 0.5s ease-out, transform 0.5s ease-out, -webkit-transform 0.5s ease-out;
    will-change: opacity, visibility;
}

.fade-in-left.is-visible {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
    visibility: visible;
}
.flip-container {
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}

.flip-container:hover .flipper, .flip-container.hover .flipper {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

.flip-container, .front, .back {
    width: 90px;
    height: 90px;
}

.flipper {
    -webkit-transition: 0.6s;
    -webkit-transform-style: preserve-3d;
    -moz-transition: 0.6s;
    -moz-transform-style: preserve-3d;
    -o-transition: 0.6s;
    -o-transform-style: preserve-3d;
    transition: 0.6s;
    transform-style: preserve-3d;
    position: relative;
}

.front, .back {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
}

.front {
    z-index: 2;
}

.back {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}
.front .skill, .back .skill {
    padding: 1.5rem;
    font-weight: bold;
    font-size: 3.5rem;
}

.back .skill p {
    font-family: 'Oswald', serif;
    letter-spacing: 0.1rem;
    box-sizing: border-box;
}
.loader {
    position: absolute;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    transition: all 400ms;
    -webkit-animation: fadeOut 2s ease-out;
            animation: fadeOut 2s ease-out;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.hidden {
    opacity: 0;
    z-index: -10;
}

.ball {
    width: 30px;
    height: 30px;
    background: peru;
    border-radius: 50%;
    margin: 0.2rem;
    -webkit-animation: ball 1s infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);
            animation: ball 1s infinite cubic-bezier(0.445, 0.05, 0.55, 0.95);
    -webkit-transform: scale(0);
            transform: scale(0);
}

.ball-1 {
    background-color: #ffa69e;
}

.ball-2 {
    background-color: #aed9e0;
}

.ball-3 {
    background-color: #75E6CF;
}

.ball-4 {
    background-color: #ffa69e;
}

.ball-1 {
    left: 20px;
    -webkit-animation-delay: 0;
            animation-delay: 0;
}

.ball-2 {
    left: 100px;
    -webkit-animation-delay: .15s;
            animation-delay: .15s;
}

.ball-3 {
    left: 180px;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}

.ball-4 {
    left: 260px;
    -webkit-animation-delay: .45s;
            animation-delay: .45s;
}

@-webkit-keyframes ball {
    0%, 25% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80%, 100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@keyframes ball {
    0%, 25% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    80%, 100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    80% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    80% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
html, body, #root {
    margin: 0;
    padding: 0;
}

#nav a button {
    font-family: 'Oswald', sans-serif;
    font-size: 1rem;
}

.signature {
    font-family: 'Allison', cursive;
    font-size: 1.5em;
}

@media only screen and (max-width: 1300px) {
    #heading h1 {
        font-size: 3.5rem;
    }
    #heading h5 {
        font-size: 1.5rem;
        margin: 1.3rem 0;
    }
    #heading .signature {
        display: block;
        font-size: 2em;
    }
}

@media only screen and (max-width: 600px) {
    #heading h1 {
        font-size: 2.2rem;
    }
    #heading .signature {
        font-size: 1.8em;
    }
    #heading h5 {
        font-size: 1.2rem;
        line-height: 2rem;
        margin: 1rem 0;
    }
    #heading a svg {
        font-size: 3rem;
    }
    #nav a button {
        font-size: 0.8rem;
    }
}
